<template>
    <v-dialog v-model="dialog" :max-width="600" :style="`z-index: ${options.zIndex};`" @keydown.esc="close" id="background-container-2" class="background-container-2">
        <v-card 
            id="background-2" 
            class="background-2" 
            v-if="src"
        >
            <v-card-actions style="background-color: var(--v-primary-base);">
                <h3 style="color:white">
                    {{ src.split('/')[src.split('/').length-1].split('.').filter((_,i,a)=>i!==a.length-2).join('.') }}
                </h3>
                <v-spacer></v-spacer>
                <v-btn icon @click.native="close" style="cursor: pointer;"><v-icon>mdi-close</v-icon></v-btn>
            </v-card-actions>
            <v-divider></v-divider>
            <img 
                :src="src" 
                style="max-width: 600px; max-height: 600px; width: auto; height: auto; display: block; margin: 0 auto;"
            />
        </v-card>
    </v-dialog>
</template>

<script>
import Trianglify from "trianglify";
export default {
    data: () => ({
        dialog: false,
        src:'',
        options: {
            color: 'primary',
            zIndex: 200
        },
        svg:{}
    }),
    methods: {
        open(src) {
            this.src = src
            this.dialog = true
            this.$nextTick(() => {
                this.svgNew();
            });
        },
        close() {
            this.dialog = false
        },
        svgNew() {
            const pattern = Trianglify({
                width: 600,
                height: 600,
                cellSize: 75,
                variance: 0.75,
                xColors: ['#f893b2ff', '#ffffff', '#f893b2ff'],
            });

            this.svg.width = "600px";
            this.svg.height = "600px";
            this.svg.pattern = pattern;

            const canvas = this.svg.pattern.toCanvas();
            const dataUrl = canvas.toDataURL();

            const bg = document.getElementById("background-2");
            if (bg) {
                bg.style.background = `url(${dataUrl})`;
            }
        },
    },
}
</script>

<style scoped>
.zoom-image {
  object-fit: contain !important;
  width: 100%;
  height: auto !important;
  max-width: 600px;
}
</style>